import { gql } from '@apollo/client';

export const LOGIN_MEMBER = gql`
  mutation loginMember($input: LoginMemberInput!) {
    loginMember(input: $input) {
      accessToken
      member {
        email
        isActive
        memberCompanies {
          companyId
        }
        memberRoles {
          role {
            name
          }
        }
        memberProjects {
          memberProjectRoleProject {
            projectRole {
              name
              codeName
              label
              project {
                id
                name
              }
            }
          }
        }
        profile {
          firstName
          lastName
          phone
          photo
          memberId
          country {
            name
          }
        }
      }
    }
  }
`;

export const PROJECTSBYID = gql`
  query projectById($id: UUID!) {
    projectById(id: $id) {
      id
      name
      description
      logo
      tagLine
      url
      companyId
      projectRoles {
        name
        codeName
        id
        createdAt
        updatedAt
      }
      memberProjects {
        memberId
      }
      id
      createdAt
      updatedAt
      services {
        id
        name
        label
        codeName
        image
        description
        priority
        updatedAt
        createdAt
        projectMicroservices {
          id
          projectId
        }
      }
    }
  }
`;
